.l-scroll {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.l-scroll-up {
  padding-top: 1.875rem;
}
.l-scroll-down {
  padding-bottom: 1.875rem;
}
.l-scroll-loading-up {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0.3125rem;
  height: 3.75rem;
  margin: auto;
  z-index: 100;
  text-align: center;
}
.l-scroll-loading-down {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: 1.875rem;
  margin: auto;
  z-index: 100;
  text-align: center;
}
